import { ChangeDetectorRef, Component, OnInit, ViewChild } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { MatSnackBar } from '@angular/material/snack-bar';
import { ActivatedRoute, Router } from '@angular/router';
import { ApiService } from 'app/shared/services/api.service';
import { DataService } from 'app/shared/services/data.service';
import { LocalStoreService } from 'app/shared/services/local-store.service';
import { AuditLogComponent } from '../../audit-log/audit-log.component';
import Swal from 'sweetalert2';

@Component({
  selector: 'app-add-charge-mant',
  templateUrl: './add-charge-mant.component.html',
  styleUrls: ['./add-charge-mant.component.scss']
})
export class AddChargeMantComponent implements OnInit {

  public chargeMaint: FormGroup;
  @ViewChild('auditLog') auditLog : AuditLogComponent
  chargeMaintIsEdit: any;
  isEdit: boolean = false ;
  
  loggedInUser : any;
  auditLogData : any;
  entityCodeArr: any[]=[];
  currencyId: any;
  readorWrite: boolean = true;
  currencyCode: any;
  id: any;
  entityData: any;
  bankDetails: any;
  bankCodeArr: string[] = [];
  bankData: any;
  branchData: any;
  branchCodeArr: string[] = [];
  productData: any;
  productCodeArr: string[] = [];
  currencyData: any;
  currencyCodeArr: string[] = [];
  accountClassData: any;
  accountClassArr: string[] = [];
  customerData: any;
  customerArr: string[] = [];
  terminalData: any;
  terminalArr: string[] = [];
  entityCode: any;
  chargeId: any;
  constructor(
    private fb: FormBuilder,
    private route : ActivatedRoute,
    private api: ApiService,
    private apiService: ApiService,
    private dataService : DataService,
    private ls : LocalStoreService,
    private snack: MatSnackBar,
    private cdr: ChangeDetectorRef,
    private router:Router,
  ) { }

  ngOnInit(): void {
    this.loggedInUser = this.ls.getItem('ICUST_USER');
    console.log(this.loggedInUser);
    this.getEntityDropdown();
    this.getBankDropdown();
    this.getBranchDropdown();
    this.getProductDropdown();
    this.getCurrencyDropdown();
    this.getAccountDropdown();
    this.getCustomerDropdown();
    this.getTerminalDropdown();
    this.buildItemForm({});
    this.route.queryParamMap.subscribe((params: any) => {
      console.log(params);
      // this.entityCode = JSON.parse(params.get('entityCode'));
      this.chargeMaintIsEdit = params.get('isEdit');
      console.log("this.chargeMaintIsEdit",this.chargeMaintIsEdit);
      if(this.chargeId){
        this.isEdit = true;
        console.log(this.chargeId);
        this.apiService.editChargeMaintById(this.chargeId).subscribe((resp) =>{
           console.log(resp);
           this.id = resp.id;
           this.buildItemForm(resp);
           this.auditLogData = resp;
           this.dataService.setAuditLogData(this.auditLogData);
           this.dataService.sendClickEvent();
        });
      }
      else{
        this.readorWrite=false;
        this.buildItemForm({});
      }
    });
    this.cdr.markForCheck();
  }

  buildItemForm(item) {
    this.chargeMaint = this.fb.group({
      id:[item.id || null],
      entityCode: [item.entityCode || '', Validators.required],
      bankCode: [item.bankCode || '', Validators.required],
      branchCode: [item.branchCode || '', Validators.required],
      accountClass: [item.accountClass || ''],
      source: [item.source || ''],
      terminal: [item.terminal || ''],
      productCode: [item.productCode || ''],
      currencyCode: [item.currencyCode || '',Validators.required],
      customerGroup: [item.customerGroup || '',Validators.required],
      rateClass: [item.rateClass || ''],
      rateType: [item.rateType || ''],
      amountBasicBase: [item.amountBasicBase || ''],
    });
  }

  getEntityDropdown() {
    this.apiService.getEntityDropdown().subscribe((data) => {
      console.log(data);
      this.entityData=data;
      for(let i=0; i<data.length;i++){
        if(data[i].authStatus==='A'){
          this.entityCodeArr.push(data[i]);
        }
      }
    });
  }

  getBankDropdown(){
    this.apiService.getBankCode().subscribe(bankCodeResp=>{
      console.log(bankCodeResp);
      this.bankData=bankCodeResp;
       for(let i=0;i<bankCodeResp.length;i++){
         if(bankCodeResp[i].authStatus==='A'){
           console.log('auth',bankCodeResp[i].authStatus)
           this.bankCodeArr.push(bankCodeResp[i]);
         }
       }
     });
  }

  getBranchDropdown(){
    this.apiService.getBranchCode().subscribe(branchCodeResp=>{
      console.log(branchCodeResp);
      this.branchData=branchCodeResp;
       for(let i=0;i<branchCodeResp.length;i++){
         if(branchCodeResp[i].authStatus==='A'){
           console.log('auth',branchCodeResp[i].authStatus)
           this.branchCodeArr.push(branchCodeResp[i]);
         }
       }
    });
  }

  getProductDropdown(){
    this.apiService.getProductDetails().subscribe(productCodeResp=>{
      console.log(productCodeResp);
      this.productData=productCodeResp;
       for(let i=0;i<productCodeResp.length;i++){
         if(productCodeResp[i].authStatus==='A'){
           console.log('auth',productCodeResp[i].authStatus)
           this.productCodeArr.push(productCodeResp[i]);
         }
       }
    });
  }

  getCurrencyDropdown(){
    this.apiService.fetchCurrencyMaintDetails().subscribe(currencyCodeResp=>{
      console.log(currencyCodeResp);
      this.currencyData=currencyCodeResp;
       for(let i=0;i<currencyCodeResp.length;i++){
         if(currencyCodeResp[i].authStatus==='A'){
           console.log('auth',currencyCodeResp[i].authStatus)
           this.currencyCodeArr.push(currencyCodeResp[i]);
         }
       }
    });
  }

  getAccountDropdown(){
    this.apiService.fetchAccountClassDetails().subscribe(accountClassResp=>{
      console.log(accountClassResp);
      this.accountClassData=accountClassResp;
       for(let i=0;i<accountClassResp.length;i++){
         if(accountClassResp[i].authStatus==='A'){
           console.log('auth',accountClassResp[i].authStatus)
           this.accountClassArr.push(accountClassResp[i]);
         }
       }
    });
  }

  getCustomerDropdown(){
    this.apiService.fetchCustomerGroupDetails().subscribe(customerResp=>{
      console.log(customerResp);
      this.customerData=customerResp;
       for(let i=0;i<customerResp.length;i++){
         if(customerResp[i].authStatus==='A'){
           console.log('auth',customerResp[i].authStatus)
           this.customerArr.push(customerResp[i]);
         }
       }
    });
  }

  getTerminalDropdown(){
    this.apiService.fetchTerminalDetails().subscribe(terminalResp=>{
      console.log(terminalResp);
      this.terminalData=terminalResp;
       for(let i=0;i<terminalResp.length;i++){
         if(terminalResp[i].authStatus==='A'){
           console.log('auth',terminalResp[i].authStatus)
           this.terminalArr.push(terminalResp[i]);
         }
       }
    });
  }

  submit() {
    this.chargeMaint.value.loggedInUser = this.loggedInUser.userName;
    if(this.isEdit == true){
      this.chargeMaint.value.id = this.id
    }
    console.log(this.chargeMaint.value);
    this.apiService.upsertChargeMaintDetails(this.chargeMaint.value).subscribe(data => {
      console.log("data",data);
      if(data.status == 400){
        this.snack.open(`${data.message}`, 'OK', {
          duration: 4000,
          verticalPosition: 'top',
          horizontalPosition: 'right'
        });
      }
      else if(data && this.isEdit == false ){
        this.snack.open('Data Created Successfully!', 'OK', {
          duration: 4000,
          verticalPosition: 'top',
          horizontalPosition: 'right'
        });
        this.router.navigate([`/maintenance/main/chargeMaint`]);
        this.cdr.markForCheck();
      }
     else if(data && this.isEdit == true){
        this.snack.open('Data Updated!', 'OK', {
          duration: 4000,
          verticalPosition: 'top',
          horizontalPosition: 'right'
        });
        this.router.navigate([`/maintenance/main/chargeMaint`]);
        this.cdr.markForCheck();
      }
      else {
        this.snack.open('Save Failed! Cannot add duplicate records', 'OK', {
          duration: 4000,
          verticalPosition: 'top',
          horizontalPosition: 'right'
        });
      }
    })
    
  }

  goBack() {
    this.router.navigate([`/maintenance/main/chargeMaint`]);
  }

  deleteRecord(operation){
    Swal.fire(
      {
        text: 'You are trying to '+ operation +' record.' + ' Do you want to proceed?',
        showCancelButton: true,
        confirmButtonColor: '#3085d6',
        cancelButtonColor: '#d33',
        cancelButtonText: 'NO',
        confirmButtonText: 'YES', 'icon': 'info'
      }
    ).then((result => {
      if(result.isConfirmed==true){
        this.apiService.deleteChargeMaint(this.auditLogData.id)
        .subscribe(data => {
            this.router.navigate([`/maintenance/main/chargeMaint`]);
        },
        err=>{
          if (err.status == 200) {
            this.snack.open('Record Deleted Successfully', 'OK', {
              duration: 4000,
              verticalPosition: 'top',
              horizontalPosition: 'right'
            });
          }
            this.router.navigate([`/maintenance/main/chargeMaint`]);
      })
      }}))
  }

 editRecord(){
 this.readorWrite=false;
 }

  updateRecord(operation) {
    Swal.fire(
      {
        text: 'You are trying to '+ operation +' record.' + ' Do you want to proceed?',
        showCancelButton: true,
        confirmButtonColor: '#3085d6',
        cancelButtonColor: '#d33',
        cancelButtonText: 'NO',
        confirmButtonText: 'YES', 'icon': 'info'
      }
    ).then((result => {
      if(result.isConfirmed==true){
        console.log("operation::",operation);
        console.log(this.chargeMaint.value.id);
        let payload: any = {};
        payload.operation = operation;
        payload.loggedInUser = this.loggedInUser.userName;
        payload.id = this.id
        this.apiService.updateAuditLogForChargeMaint(payload).subscribe((data) => {
          console.log(data);
          this.auditLogData = data;
          this.dataService.setAuditLogData(this.auditLogData);
          this.dataService.sendClickEvent();
          let message = '';
          switch (operation) {
            case 'open':
              message = 'opened'
              break;
            case 'close':
              message = 'closed'
              break;
            case 'auth':
              message = 'authorized'
              break;
            default:
              break;
          }
          this.snack.open(`Record ${message} successful!'`, "OK", {
            duration: 2000,
            verticalPosition: "top",
            horizontalPosition: "right",
          });
        });
      }}))
    
  }
  dataByEntityCode(index,entity){
    return entity.entityCode;
  }
  dataByBankCode(index,item){
    return item.bankCode;
  }
  dataByBranchCode(index,item){
    return item.branchCode;
  }
  // dataByProductCode(index,item){
  //   return item.productCode;
  // }
  dataByCurrencyCode(index,item){
    return item.currencyCode;
  }
  // dataByAccountClass(index,item){
  //   return item.accountClass;
  // }
  dataByCustomer(index,item){
    return item.customer;
  }
  // dataByTerminal(index,item){
  //   return item.terminal;
  // }
}

