import { ChangeDetectorRef, Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { MatSnackBar } from '@angular/material/snack-bar';
import { ActivatedRoute, Router } from '@angular/router';
import { ApiService } from 'app/shared/services/api.service';
import { DataService } from 'app/shared/services/data.service';
import { LocalStoreService } from 'app/shared/services/local-store.service';
import * as moment from 'moment';

@Component({
  selector: 'app-add-branch',
  templateUrl: './add-branch.component.html',
  styleUrls: ['./add-branch.component.scss']
})
export class AddBranchComponent implements OnInit {
  bankCodeArr:string[]=[];
  entityCodeArr:string[]=[];
  branchCodeArr:string[]=['SBIN123','SBIN231','ABCN212','ABC235','AB879'];
  branchCode:string[]=[];
  BranchHolidayForm: FormGroup;
  isEdit: boolean = false;
  isReadOnly: boolean = true;
  id: any;
  loggedInUser : string;
  auditData : any;
  isAuditData : boolean = false;
  show = false;

  constructor(private formBuilder: FormBuilder,private apiService:ApiService,
    private router:Router,private snack: MatSnackBar, private route : ActivatedRoute,
    private dataService : DataService,    private ls : LocalStoreService,
    
    private cdr: ChangeDetectorRef) { this.BranchHolidayForm = this.formBuilder.group({
      branchCode: [ '',[Validators.required]],
      transactionDate: ['',[Validators.required]],
      bankCode: ['',[Validators.required]],
      year:[''],
      id: [ ''],
      
      entityCode: ['',[Validators.required]],

    })}

  ngOnInit(): void {
    //this.getBranchCode(); 
    //this.buildItemForm();
    this.getBankCode();
    this.getEnityCode();
    this.route.queryParamMap.subscribe((params: any) => {
      if(params.get('id')){
        this.isEdit = true;
        this.id = params.get('id');
        this.apiService.editeditBranchHoliday(params.get('id')).subscribe((resp) =>{
           this.buildItemForm(resp);
           this.auditData = resp
          this.isAuditData = true;
          this.dataService.setAuditLogData(this.auditData);
          console.log("this.auditData", this.auditData)
        });
      }
      else{
        this.buildItemForm({});
      }
    }
    );
    this.loggedInUser = this.ls.getItem('ICUST_USER').userName;
  }
  getBranchCode() {
    this.apiService.getBranchCode().subscribe((resp) => {
      this.branchCode = resp;
    });
  }

  getBankCode() {
    this.apiService.getBankCode().subscribe(bankCodeResp => {
      console.log(bankCodeResp);
      for(let i=0; i<bankCodeResp.length;i++){
        if(bankCodeResp[i].authStatus==='A'){
          this.bankCodeArr.push(bankCodeResp[i]);
        }
      }
      console.log(this.bankCodeArr);
      // this.bankCode = bankCodeResp;
    });
  }

  getEnityCode() {
    this.apiService.fetchEntityDetails().subscribe(entityCodeResp => {
      console.log(entityCodeResp);
      for(let i=0; i<entityCodeResp.length;i++){
        if(entityCodeResp[i].authStatus==='A'){
          this.entityCodeArr.push(entityCodeResp[i]);
        }
      }
      console.log(this.entityCodeArr);
      // this.entityCode = entityCodeResp;
    });
  }
  
  goBack(){
   this.router.navigate(["/maintenance/main/holiday/BranchHoliday"]);
  }

  updateRecord(operation) {
    let payload: any = {};
    payload.id = this.id;
    payload.operation = operation;
    payload.loggedInUser = this.loggedInUser;
    console.log(payload);
    console.log(this.loggedInUser);
    
    
    this.apiService.getAuditLogsForHolidayBranch(payload).subscribe((data) => {
      this.auditData = data;
      this.dataService.setAuditLogData(this.auditData);
      this.isAuditData = true;
      this.dataService.sendClickEvent();
      let message = '';
      switch (operation) {
        case 'open':
          message = 'opened'
          break;
        case 'close':
          message = 'closed'
          break;
        case 'auth':
          message = 'authorized'
          break;
        default:
          break;
      }
      this.snack.open(`Record ${message} successful!'`, "OK", {
        duration: 2000,
        verticalPosition: "top",
        horizontalPosition: "right",
      });
    });
  }

  buildItemForm(item) {

    this.BranchHolidayForm = this.formBuilder.group({
      branchCode: [item.branchCode || '',[Validators.required]],
      transactionDate: [item.transactionDate ||'',[Validators.required]],
      bankCode: [item.bankCode ||'',[Validators.required]],
      year:[''],
      id: [ item.id ||''],
      
      entityCode: [ item.entityCode ||'',[Validators.required]],

    })
  }
  submit(){
    
    let now = new Date();
    now=this.BranchHolidayForm.value.transactionDate;
    var dateStringWithTime = moment(now).format('YYYY-MM-DD HH:MM:SS');
    console.log(this.BranchHolidayForm.value)
    console.log(dateStringWithTime)
   var convertedyear=dateStringWithTime.slice(0,4);
   var convertMonth=dateStringWithTime.slice(5,7);
   var conv1Month= parseInt(convertMonth);
   var convertCalender=dateStringWithTime.slice(8,10);
   var conv1Calender= parseInt(convertCalender);
   
  
    let payload=[{
      entityCode: this.BranchHolidayForm.value.entityCode,
      bankCode: this.BranchHolidayForm.value.bankCode,
      branchCode: this.BranchHolidayForm.value.branchCode,
      transactionDate:this.BranchHolidayForm.value.transactionDate,
      calenderYear:convertedyear,
      id:this.BranchHolidayForm.value.id,
      calenderMonth:conv1Month,
      calenderDate:dateStringWithTime,
      loggedInUser:this.loggedInUser
    }
  ]
  this.apiService.updateBranchHolidayDetails(payload,false).subscribe(response => {
    if(response != null){
      this.snack.open(`Record Added!'`, "OK", {
        duration: 2000,
        verticalPosition: "top",
        horizontalPosition: "right",
      });
      this.cdr.markForCheck();
    }
    this.router.navigate(["/maintenance/main/holiday/BranchHoliday"]);
  });
  

  
  }
  dataByEntity(index,entityC){
    return entityC.entityCode;
  }
  dataByBank(index,bankC){
    return bankC.bankCode;
  }
  dataByBranch(index,branch){
    return branch.branchCode;
  }

}
