  getEntityCode(){
    this.api.fetchEntityDetails()
      .subscribe(data => {
        console.log(data);
        for(let i=0;i<data.length;i++){
          if (data[i].authStatus=='A') {
            this.entityArr.push(data[i].entityCode)
          }
        }
        console.log(this.entityArr);
        
      });
  }
