  getBankCode(entityCode:any) {
    this.bankCodeArr=[];
    this.entityName=entityCode.value;
    this.api.getBankInfoByEntityCode(entityCode.value).subscribe((data) => {
      for(let i=0;i<data.length;i++){
                if (data[i].authStatus=='A') {
                  this.bankCodeArr.push(data[i].entityCode)
                }
              }
      console.log(this.bankCodeArr);
    });
  }
