  buildUserForm(data){
    console.log(data);
    this.userMaintenance = this.formBuilder.group({
      userId: [data ? data.userId : '',[Validators.required]],
      userName: [data ? data.userName : '',[Validators.required]],
      email: [data ? data.email : '',[Validators.required]],
      mobile: [data ? data.mobile : '',[Validators.required,Validators.minLength(10), Validators.maxLength(10)]],
      roleId: [data ? data.roleId : '',[Validators.required]],
      firstName: [data ? data.firstName : '',[Validators.required]],
      lastName: [data ? data.lastName : '',[Validators.required]],
      entityCode: [data ? data.entityCode : '',[Validators.required]],
      branchCode: [data ? data.branchCode : '',[Validators.required]],
      department:[data ? data.department : '',[Validators.required]],
      levelForUser:[data ? data.levelForUser : '',[Validators.required]],
      finance:[data ? data.finance : '',[Validators.required]],
      intime: [data.intime ? data.intime : '09:00'],
      outtime: [data.outtime ? data.outtime : '06:00'],
      bankCode: [data ? data.bankCode : '',[Validators.required]],
      userStatus:[data? data.userStatus:'', [Validators.required]],
      // language: [data ? data.language : ''],
      
      password: new FormControl("", [
        
        Validators.compose([
          Validators.pattern(this.passwordRegex),
          Validators.maxLength(this.maxPass),
          Validators.minLength(this.minPass),
        ]),
      ]),
      passwordGenerationType: [data ? data.passwordGenerationType : ''],
      tillId: [data ? data.tillId : '',[Validators.required]],
      
    });
    this.formControlValueChanged();
    var role
    if(data){
      role= this.RoleInfo.filter((item)=>{
        return data.roleId == item.id;
      });
    }
    this.tillData = this.till.find(x => x.id == data.tillId);
    console.log("this.tillData::",this.tillData)
    console.log("role::",role)
    this.api.getSecurityPolicyDetails().subscribe((resp) => {
      this.security1 = resp;
      console.log(this.security1);
      this.ucase = this.security1[0].pswdComplexUcase;
      this.lcase = this.security1[0].pswdComplexLcase;
      console.log(this.lcase);
      this.num = this.security1[0].pswdComplexNum;
      this.splc = this.security1[0].pswdComplexSplc;
      this.minPass = this.security1[0].minPswdLength;
      this.maxPass = this.security1[0].maxPswdLength;
      console.log(this.maxPass);

      if (this.security1[0].pswdComplexUcase=="true") {
        this.passwordRegex = '(?=[^A-Z]*[A-Z])';
        this.errorMsg = ' UpperCase';
      }
      if (this.security1[0].pswdComplexLcase=="true") {
        this.passwordRegex = this.passwordRegex + '(?=[^a-z]*[a-z])';
        if (this.errorMsg != '') {
          this.errorMsg = this.errorMsg + ', LowerCase';
        }
        else {
          this.errorMsg = this.errorMsg + ' LowerCase';
        }
      }
      if(this.splc=="true") {
        this.passwordRegex = this.passwordRegex  + '(?=.*[!@#$%^&*])';
        if(this.errorMsg != '') 
          this.errorMsg = this.errorMsg + ", Special Character";  
        else
          this.errorMsg = this.errorMsg + " Special Character";
      }

      if (this.security1[0].pswdComplexNum=="true") {
        this.passwordRegex = this.passwordRegex + '(?=[^0-9]*[0-9])';
        if (this.errorMsg != '') {
          this.errorMsg = this.errorMsg + ', Number';
        }
        else {
          this.errorMsg = this.errorMsg + ' Number';
        }
      }

      this.passwordRegex =
        this.passwordRegex + ".{" + this.minPass + "," + this.maxPass + "}";
      this.errorMsg =
        "Password must contain atleast one " + this.errorMsg + ".";
      console.log(this.passwordRegex);


      // this.userMaintenance = this.formBuilder.group({
     
      //   password: new FormControl("", [
      //     Validators.required,
      //     Validators.compose([
      //       Validators.pattern(this.passwordRegex),
      //       Validators.maxLength(this.maxPass),
      //       Validators.minLength(this.minPass),
      //     ]),
      //   ]),
      // });
    });
    
  }
    formControlValueChanged(){
      let cifNumberControl = this.userMaintenance.get('password');
    this.userMaintenance.get('passwordGenerationType').valueChanges.subscribe(
        (val) => {
            console.log(val);
            if (val == 'Manual') {
              this.userMaintenance.get('password').setValidators([Validators.required]);
            }   
            else{
              this.userMaintenance.get('password').setValidators([Validators.nullValidator]);
          }            
            cifNumberControl.updateValueAndValidity();
        });
    }

    onInputChange() {
    
      (this.userMaintenance.get('password') as FormControl).setValidators([
        Validators.compose([
          Validators.pattern(this.passwordRegex),
          Validators.maxLength(this.maxPass),
          Validators.minLength(this.minPass),
        ]),
      ]);
      this.isDisabledSave = false
    }

  onInput(event:any){
    console.log(event);
    this.UserName= event.target.value.toLocaleUpperCase();
    this.userMaintenance.get('userId').setValue(this.UserName)
  }
