export class SecurityDto {
  clientId: number;
  maxInvLogins: number;
  maxPswdLength: number;
  minPswdLength: number;
  pswdExpiry: number;
  notifyPasswordExpiryInDays: number;
  pswdReuseAft: number;
  pswdComplexLcase: any;
  pswdComplexNum: any;
  pswdComplexSplc: any;
  passwordGenerationType:any
  pswdComplexUcase: any;
  authStatus: string;
  authorizedBy: string;
  authorizedTime: Date;
  modifiedBy: string;
  modifiedTime: Date;
  pswdComplexity: string;
  version: number;
}
