import { ChangeDetectorRef, Component, OnInit, ViewChild } from '@angular/core';
import { FormArray, FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { MatSnackBar } from '@angular/material/snack-bar';
import { ActivatedRoute, Router } from '@angular/router';
import { ApiService } from 'app/shared/services/api.service';
import { AppLoaderService } from 'app/shared/services/app-loader/app-loader.service';
import { DataService } from 'app/shared/services/data.service';
import { LocalStoreService } from 'app/shared/services/local-store.service';
import { AuditLogComponent } from '../../audit-log/audit-log.component';
import { auditLogInterface, loggedInUserInterface } from './add-kiosk-maintenance';
import Swal from 'sweetalert2';


@Component({
  selector: 'app-add-kiosk-login-maintenance',
  templateUrl: './add-kiosk-login-maintenance.component.html',
  styleUrls: ['./add-kiosk-login-maintenance.component.scss']
})
export class AddKioskLoginMaintenanceComponent implements OnInit {
  @ViewChild('auditLog') auditLog : AuditLogComponent
  addKioskDetailsForm: FormGroup;
  data:any;
  entityCode: any[] = [];
  bankCode: any[] = [];
  branchCode: string[] = [];
  entity: any;
  loggedInUser : any;
  isReadOnly: boolean = true;
  isEdit: boolean = false;
  isView: boolean = true;
  show = false;
  editNow : boolean = false;
  entityIsEdit: any;
  id: any;
  auditData : any;
  isAuditData : boolean = false;
  auditLogData: auditLogInterface;
  entityCodeArr: any[] = [];
  readorWrite: boolean=true;
  kioskEnable: boolean = true;
  constructor(private formBuilder: FormBuilder,private apiService:ApiService,
    private router:Router,private snack: MatSnackBar, private route : ActivatedRoute,    private dataService : DataService, 
    private ls : LocalStoreService, private loader: AppLoaderService,

    private cdr: ChangeDetectorRef) {
}

  ngOnInit(): void {
    this.loggedInUser = this.ls.getItem('ICUST_USER').userName
    this.getEntityDropdown();    
    this.getBankCode();
     this.getBranchCode();
    this.route.queryParamMap.subscribe((params: any) => {
      console.log(params.get('id'));
      this.entityIsEdit = params.get('isEdit');
      console.log("this.entityIsEdit",this.entityIsEdit);
      if(params.get('isEdit') == 'Yes'){
         this.isReadOnly = false;
       this.isView= true;
       this.editNow = true;
      }
      else if(params.get('isEdit') == 'No'){
         this.isReadOnly = true;
         this.isView= true;
        this.editNow = false;
      }
      else if(params.get('isEdit') == 'false'){
        this.isReadOnly = false;
        this.isView= false;
        this.editNow = false;
      }
      else if(params.get('isEdit') == null){
      this.isView= false;
      }
      if(params.get('id')){
        this.isEdit = true;
        this.id = params.get('id');
          this.apiService.getKioskDataById(params.get('id')).subscribe((resp) =>{
          console.log(resp);
          if(resp){
            this.kioskEnable = resp.kioskSystemEnable;
            this.readorWrite = true;
            this.buildItemForm(resp);
            resp.otpInfo.map(otp => {
              if(otp.emailId){
                this.pushEmail(otp)
              }
              if(otp.mobileNumber){
                this.pushMobile(otp)
              }
            })
            console.log(this.addKioskDetailsForm);
            
            this.auditLogData = resp;
            this.isAuditData = true;
            this.dataService.setAuditLogData(this.auditLogData);
            this.dataService.sendClickEvent();
          }
       });
      }
      else{
        this.readorWrite=false;
       this.buildItemForm({});
this.pushEmail();
this.pushMobile();
      }
    }
    ); 
  }
  buildItemForm(item) {
    console.log("item",item)
      this.addKioskDetailsForm = this.formBuilder.group({
      entityCode: [item.entityCode ||'',[Validators.required]],
      bankCode: [item.bankCode || '',Validators.required],
      branchCode: [ item.branchCode || '',Validators.required],
      id: [ item.id ||''],     
      loggedInUser: [item.loggedInUser || ''],     
      kioskSystemCode: [ item.kioskSystemCode || '',[Validators.required]],
      kioskSystemName: [ item.kioskSystemName || '',[Validators.required]],
      passcode: [ item.passcode,[Validators.required]],
      logoutCode: [ item.logoutCode || '',[Validators.required]],
      kioskSystemEnable: [ this.kioskEnable],
      otpEmailData: new FormArray([]),
      otpMobileData: new FormArray([])      
    });
    console.log(this.addKioskDetailsForm);  
    this.addKioskDetailsForm.get('passcode').valueChanges.subscribe(val => this.addKioskDetailsForm.get('passcode').setValidators(Validators.pattern("(?=.*[0-9])(?=.*[a-zA-Z])([a-zA-Z0-9]+){6,6}$")))  
    this.addKioskDetailsForm.get('logoutCode').valueChanges.subscribe(val => this.addKioskDetailsForm.get('logoutCode').setValidators(Validators.pattern("(?=.*[0-9])(?=.*[a-zA-Z])([a-zA-Z0-9]+){6,6}$")))  

  }

  get f() { return this.addKioskDetailsForm.controls; }
  get e() { return this.f.otpEmailData as FormArray; }
  get m() { return this.f.otpMobileData as FormArray;}

  removeOtpData(index){
    this.e.removeAt(index)
  }
  removeMobileData(index){
    this.m.removeAt(index)
  }
  pushEmail(item?) {
    this.e.push(this.formBuilder.group({
      id:[item ? item.id : ''],
      emailId: [item ? item.emailId : '', [Validators.required,Validators.pattern('^[A-Za-z0-9._%+-]+@[a-z0-9.-]+\\.[a-z]{2,4}$')]]
    }));
  }
  pushMobile(item?) {
    this.m.push(this.formBuilder.group({
      id: [item ? item.id : ''],
      mobileNumber: [item ? item.mobileNumber : '', [Validators.required,Validators.pattern("^[0-9]{10,12}$")]]
    }));
  }
  getBranchCode(){
    this.apiService.getBranchCode().subscribe(branchCodeResp=>{
      this.branchCode=branchCodeResp;
      console.log(this.branchCode);
    });
  }
  getBankCode() {
    this.apiService.getBankCode().subscribe((resp) => {
     // this.bankCode = resp;
      for(let i=0;i<resp.length;i++){
        if(resp[i].authStatus == 'A'){
          this.bankCode.push(resp[i]);
        }
      }
    });
  }

  getEntityDropdown(){
    this.apiService.getEntityDropdown().subscribe((data) => {
      console.log(data);
      for(let i=0; i<data.length;i++){
        if(data[i].authStatus==='A'){
          this.entityCode.push(data[i]);
          console.log('auth',i)
        }
      }
      
    });   
  }
  check(){
    console.log("As");
    
  }
  submit() {
    console.log("Form:",this.addKioskDetailsForm.value);
    console.log(this.addKioskDetailsForm.get('otpEmailData'));
    console.log(this.addKioskDetailsForm.get('otpEmailData')['controls']);
    
    console.log(this.addKioskDetailsForm.controls.otpEmailData);
    console.log(this.addKioskDetailsForm.controls.otpMobileData);    
    if(this.addKioskDetailsForm.invalid){
    return;
    }
    else if(this.e.controls.length>this.m.controls.length){
      this.snack.open('Please Enter the Mobile Numbers for Email','Ok',{
        duration:3000,
        verticalPosition:"top",
        horizontalPosition:"right"
      })
      return;
    }
    else if(this.e.controls.length<this.m.controls.length){
      this.snack.open('Please Enter the EmailId for Mobile Number','Ok',{
        duration:3000,
        verticalPosition:'top',
        horizontalPosition:'right'
      })
      return;
    }
    else{
    console.log("payload:",this.addKioskDetailsForm.value);
    const formValue = this.addKioskDetailsForm.value
    let length = this.e.controls.length>this.m.controls.length?this.e.controls.length:this.m.controls.length
    let otpInfo = [];
    for(let i = 0; i < length; i++){
      let otpData = {id:'',kioskSystemId: '',emailId:'',mobileNumber:''};
      if(this.e.controls[i]){
        otpData.id = this.e.controls[i].value.id
        otpData.emailId = this.e.controls[i].value.emailId
      }
      if(this.m.controls[i]){
        otpData.mobileNumber = this.m.controls[i].value.mobileNumber
      }
      if(this.id){
        otpData.kioskSystemId = this.id
      }
      otpInfo.push(otpData)
    }
    
    const payload = {
      bankCode: formValue.bankCode,
      entityCode:formValue.entityCode,
      branchCode:formValue.branchCode,
      id:formValue.id,
      kioskSystemCode:formValue.kioskSystemCode,
      kioskSystemName:formValue.kioskSystemName,
      passcode:formValue.passcode,
      logoutCode:formValue.logoutCode,
      kioskSystemEnable:formValue.kioskSystemEnable,

      otpInfo:otpInfo,
      loggedInUser:this.loggedInUser,
    }
    console.log(payload.otpInfo[0].emailId);
    
    this.apiService.createKioskBranch(payload).subscribe(data => {
      console.log("data",data);
      if(data != null && this.isEdit == true){
        this.router.navigate([`/maintenance/main/kioskLogin`]).then(_=>{
                this.snack.open('Data Updated!', 'OK', 
                { duration: 4000 , 
                  verticalPosition: "top",
                  horizontalPosition: "right",});
                });       
        this.cdr.markForCheck();
      }
      else if(data != null || this.isEdit == true){
        console.log("data",data);
        this.router.navigate([`maintenance/main/kioskLogin`]).then(_=>{
          this.snack.open('Data Saved!', 'OK', 
          { duration: 4000 , 
            verticalPosition: "top",
            horizontalPosition: "right",});
          });
        this.cdr.markForCheck();
       }
    }) 
    }  
  }

  goBack(){
    this.router.navigate(["/maintenance/main/kioskLogin"]);
  }
  editRecord(){
    this.readorWrite=false;
    console.log(this.addKioskDetailsForm);
    
    }
    deleteRecord(operation){
      Swal.fire(
        {
          //text: 'Unable to process' + 'Error ' + this.responseforfileupload.errorMessage + 'Do you want to Proceed??',
          text: 'You are trying to '+ operation +' record.' + ' Do you want to proceed?',
          showCancelButton: true,
          confirmButtonColor: '#3085d6',
          cancelButtonColor: '#d33',
          // confirmButtonText: 'PROCEED.'
          cancelButtonText: 'NO',
          confirmButtonText: 'YES', 'icon': 'info'
        }
      ).then((result => {
        if(result.isConfirmed==true){
          this.apiService.deleteKioskLoginById(this.auditLogData.id)
          .subscribe(data => {
            this.router.navigate(["/maintenance/main/kioskLogin"]);
          },
          err=>{
            if (err.status == 200) {
              this.snack.open('Record Deleted Successfully', 'OK', {
                duration: 4000,
                verticalPosition: 'top',
                horizontalPosition: 'right'
              });
            }
            this.router.navigate(["/maintenance/main/kioskLogin"]);
          
        })
        }}))
     
           
         
    } 
    updateRecord(operation) {
      Swal.fire(
        {
          //text: 'Unable to process' + 'Error ' + this.responseforfileupload.errorMessage + 'Do you want to Proceed??',
          text: 'You are trying to '+ operation +' record.' + ' Do you want to proceed?',
          showCancelButton: true,
          confirmButtonColor: '#3085d6',
          cancelButtonColor: '#d33',
          // confirmButtonText: 'PROCEED.'
          cancelButtonText: 'NO',
          confirmButtonText: 'YES', 'icon': 'info'
        }
      ).then((result => {
        if(result.isConfirmed==true){
          let payload: any = {};
          payload.id = this.id;
          payload.operation = operation;
          payload.loggedInUser = this.loggedInUser;
          this.apiService.getAuditLogsForKioskMaintenance(payload).subscribe((data) => {
            console.log(data);
            this.auditLogData = data;
            this.dataService.setAuditLogData(this.auditLogData);
            this.dataService.sendClickEvent();
            let message = '';
            switch (operation) {
              case 'open':
                message = 'opened'
                break;
              case 'close':
                message = 'closed'
                break;
              case 'auth':
                message = 'authorized'
                break;
              default:
                break;
            }
            this.snack.open(`Record ${message} successful!'`, "OK", {
              duration: 2000,
              verticalPosition: "top",
              horizontalPosition: "right",
            });
          });
        }}))
     
    }
  dataByEntityCode(index,item){
    return item.entityCode
  }
  dataByBankCode(index,item){
   return item.bankCode
  }
  dataByBranchCode(index,item){
    return item.branchCode;
  }

  dataBybranchCode(index, branch){
    return branch.branchCode; 
 }
_keyPress(event){
  if(event.target.value.length == 10){
    event.preventDefault();
  }
  const pattern = /[0-9]/;
    let inputChar = String.fromCharCode(event.charCode);
    if (!pattern.test(inputChar)) {
      event.preventDefault();
    } 
}
_keyPress1(event){
  if(event.target.value.length == 6){
    event.preventDefault();
  }
  const pattern = /[A-Za-z0-9]/;
    let inputChar = String.fromCharCode(event.charCode);
    if (!pattern.test(inputChar)) {
      event.preventDefault();
    } 
}
}
